import NcUserStatusIcon from '@nextcloud/vue/dist/Components/NcUserStatusIcon.js';
export default {
  name: 'OnlineStatusSelect',
  components: {
    NcUserStatusIcon
  },
  props: {
    checked: {
      type: Boolean,
      default: false
    },
    type: {
      type: String,
      required: true
    },
    label: {
      type: String,
      required: true
    },
    subline: {
      type: String,
      default: null
    }
  },
  computed: {
    id() {
      return "user-status-online-status-".concat(this.type);
    }
  },
  methods: {
    onChange() {
      this.$emit('select', this.type);
    }
  }
};